<template>
  <div class="pageContent">
    <el-card shadow="always">
      <div class="pageHead">
        <div class="flex-row-between">
          <div>我的账单</div>
          <el-button type="text" @click="$router.push({path:'/assetsManage/account/record'})">我的账单</el-button>
        </div>
      </div>
      <div class="mt10 flex-row-between">
        <div v-if="$route.query.afterSaleId == 0">
          <div style="font-weight:bold;" v-if="form.settleState == 2">交易完成</div>
          <div style="font-weight:bold;" v-if="form.settleState == 3">交易关闭</div>
        </div>
        <div v-else>
          <div style="font-weight:bold;" v-if="form.settleState == 0">退款中</div>
          <div style="font-weight:bold;" v-if="form.settleState == 1">退款中</div>
          <div style="font-weight:bold;" v-if="form.settleState == 2">退款成功</div>
          <div style="font-weight:bold;" v-if="form.settleState == 3">退款成功</div>
        </div>
        <el-tag type="danger">已入账</el-tag>
      </div>
      <div class="mt10">{{form.description}} &nbsp;关联单号：{{form.orderNo}}</div>
      <div class="form mt20">
        <div class="flex-row-start">
          <div class="h bb rb flex1 tac">&nbsp;</div>
          <div class="h bb rb flex1 tac">退款金额=商品实付金额</div>
          <div class="h bb rb flex1 tac">微信手续费退回</div>
          <div v-if="form.threeList" class="h bb rb flex1 tac">佣金退回</div>
          <div v-if="form.fourList" class="h bb rb flex1 tac">邀请奖励退回</div>
          <div class="h bb flex1 tac">=实付金额（元）</div>
        </div>
        <div class="flex-row-start">
          <div class="h bb rb flex1 tac">金额</div>
          <div class="h dangerFont bb rb flex1 tac">{{form.oneList.amount}}</div>
          <div class="h dangerFont bb rb flex1 tac">{{form.twoList.amount}}</div>
          <div v-if="form.threeList" class="h dangerFont bb rb flex1 tac">{{form.threeList.amount}}</div>
          <div v-if="form.fourList" class="h dangerFont bb rb flex1 tac">{{form.fourList.amount}}</div>
          <div class="h dangerFont bb flex1 tac">{{form.fiveList.amount}}</div>
        </div>
        <div class="flex-row-start">
          <div class="h bb rb flex1 tac">出/入账时间</div>
          <div class="h bb rb flex1 tac">{{form.oneList.createTime}}</div>
          <div class="h bb rb flex1 tac">{{form.twoList.createTime}}</div>
          <div v-if="form.threeList" class="h bb rb flex1 tac">{{form.threeList.createTime}}</div>
          <div v-if="form.fourList" class="h bb rb flex1 tac">{{form.fourList.createTime}}</div>
          <div class="h bb flex1 tac">
            <div>{{form.fiveList.amount >= 0 ? '合计入账店铺金额' : '合计店铺余额支出'}}{{form.fiveList.amount}}</div>
          </div>
        </div>
        <div class="flex-row-start">
          <div class="h bb rb flex1 tac">收支说明</div>
          <div class="h bb rb flex1 tac">{{form.oneList.description}}</div>
          <div class="h bb rb flex1 tac">{{form.twoList.description}}</div>
          <div v-if="form.threeList" class="h bb rb flex1 tac">{{form.threeList.description}}</div>
          <div v-if="form.fourList" class="h bb rb flex1 tac">{{form.fourList.description}}</div>
          <div class="h bb flex1 tac">{{form.fiveList.description}}</div>
        </div>
        <div class="flex-row-start">
          <div class="h bb rb flex1 tac">流水号</div>
          <div class="h bb rb flex1 tac">{{form.oneList.orderNo}}</div>
          <div class="h bb rb flex1 tac">{{form.twoList.orderNo}}</div>
          <div v-if="form.threeList" class="h bb rb flex1 tac">{{form.threeList.orderNo}}</div>
          <div v-if="form.fourList" class="h bb rb flex1 tac">{{form.fourList.orderNo}}</div>
          <div class="h bb flex1 tac">{{form.fiveList.orderNo}}</div>
        </div>
        <div class="flex-row-start">
          <div class="h rb flex1 tac">对方</div>
          <div class="h rb flex1 tac">{{form.oneList.toUser}}</div>
          <div class="h rb flex1 tac">{{form.twoList.toUser}}</div>
          <div v-if="form.threeList" class="h rb flex1 tac">{{form.threeList.toUser}}</div>
          <div v-if="form.fourList" class="h rb flex1 tac">{{form.fourList.toUser}}</div>
          <div class="h flex1 tac">{{form.fiveList.toUser}}</div>
        </div>
      </div>
      <div class="mt20">
        <h3>关联记录</h3>
        <el-table :show-header="false" :data="form.logs" style="width: 100%">
          <el-table-column prop="createTime"></el-table-column>
          <el-table-column prop="description"></el-table-column>
          <el-table-column prop="amount"></el-table-column>
        </el-table>
      </div>
      <div class="mt20">
        <el-button @click="$router.go(-1)" type="primary">返回上一页</el-button>
      </div>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getBillDetail } from '@/api/assetsManage/myBill'
export default {
  data(){
    return{
      form:{
        oneList:{},
        twoList:{},
        threeList:{},
        fourList:{},
        fiveList:{},
        logs:[]
      },
    }
  },
  created(){
    this.getData()
  },
  methods:{
    getData(){
      getBillDetail(this.$route.query.orderId,this.$route.query.afterSaleId).then(res=>{
        console.log('我的账单（交易明细）',res)
        if(res.code ==200){
          this.form = res.data;
        }
      })
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
.form{
  width: 100%;
  font-size: 14px;
  // color: ;
  border: 1px solid rgb(144, 147, 153);
  .rb{
    border-right: 1px solid rgb(144, 147, 153);
  }
  .bb{
    border-bottom: 1px solid rgb(144, 147, 153);
  }
  .h{
    height: 44px;
    line-height: 44px;
  }
}
</style>